

import Vue from 'vue'
import App from './App'
import router from './router'

import * as custom from '@/assets/js/filter.js'
import axios from 'axios'
import Swiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload, {
preLoad: 1.3,
error: 'http://img3.imgtn.bdimg.com/it/u=31820628,1906662201&fm=26&gp=0.jpg',
loading: 'http://img.lanrentuku.com/img/allimg/1212/5-121204194025.gif',
attempt: 1
})
Vue.use(Swiper)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)

Vue.config.productionTip = false
Vue.prototype.$axios = axios


Object.keys(custom).forEach(key => {
Vue.filter(key, custom[key])
})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



